body {
  height: 100vh;
  width: 100vw;
  font: 14px "Century Gothic", Futura, sans-serif;
  color: white;
  background-color: gray;
}
input[type=button] {
  background-color: transparent;
  color: white;
}
form {
  text-align: center;
}
.field {
  margin: auto;
  height: 25vh;
  width: 80vw;
  border: 5px solid white;
  background-color: #526F35;
  display: -ms-flexbox;
  display: flex;
}
.goalieSquareLeft {
  position: absolute;
  height: 6.25vh;
  width: 3vw;
  margin-top: 9vh;
  border: 1px solid white;
}
.circle {
    position: absolute;
    height: 7vh;
    width: 7vh;
    margin-top: 9vh;
    margin-left: 38vw;
    border: 1px solid white;
    border-radius: 50%;
}
.goalieSquareRight {
  position: absolute;
  height: 6.25vh;
  width: 3vw;
  margin-top: 9vh;
  margin-left: 77vw;
  border: 1px solid white;
}
.fieldInfo {
  margin: auto;
  width: 40vw;
  text-align: center;
}
.position {
  z-index: 10;
  width: 13.3vw;
  border: 1px dashed #808080;
}
.position[name='Offense'] {
  border-right: 1px solid white;
}
.active {
  background-color: blue;
}



